'use client';

import { useEffect, useState } from 'react';
import Link from 'next/link';
import { searchJobs, type Job } from '@/lib/api';

export default function RecallsPage() {
  const [rows, setRows] = useState<Job[]>([]);
  const [loading, setLoading] = useState(false);
  const [msg, setMsg] = useState('');

  async function load() {
    setLoading(true);
    setMsg('');
    try {
      const res = await searchJobs('@recall'); // “Called” in any column
      setRows(res.rows || []);
    } catch (e: any) {
      setMsg(e?.message || 'Failed to fetch');
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => { load(); }, []);

  return (
    <main className="light-page" style={{ maxWidth: 1100, margin: '18px auto', padding: '0 14px 40px' }}>
      <h1>Recalls (Follow-ups)</h1>
      <p className="muted">Anything already marked “Called” in meat, caping, or Webbs.</p>

      <div style={{ display: 'flex', gap: 8, alignItems: 'center', marginBottom: 10 }}>
        <Link href="/reports/calls" className="btn secondary">Back to Call Report</Link>
        <div style={{ marginLeft: 'auto', fontSize: 12, color: '#334155' }}>{loading ? 'Loading…' : msg}</div>
      </div>

      <div className="table card">
        <table className="light-page" style={{ width: '100%' }}>
          <thead>
            <tr>
              <th>Tag</th><th>Customer</th><th>Phone</th>
              <th>Status</th><th>Caping</th><th>Webbs</th>
              <th>Attempts</th><th>Last Call</th>
            </tr>
          </thead>
          <tbody>
            {rows.map((r) => (
              <tr key={r.tag}>
                <td><Link href={`/intake?tag=${encodeURIComponent(r.tag || '')}`}>{r.tag}</Link></td>
                <td>{r.customer}</td>
                <td>{r.phone}</td>
                <td>{r.status}</td>
                <td>{r.capingStatus}</td>
                <td>{r.webbsStatus}</td>
                <td>{r.callAttempts ?? 0}</td>
                <td>{r.lastCallAt || '—'}</td>
              </tr>
            ))}
            {!loading && rows.length === 0 && (
              <tr><td colSpan={8}>No rows.</td></tr>
            )}
          </tbody>
        </table>
      </div>
    </main>
  );
}

